@import url('https://fonts.googleapis.com/css2?family=Inter:wght@900&family=Michroma&family=Poppins&display=swap');

.inProgressDiv {
    font-family: 'Secular One';
    letter-spacing: 2.5%;
    color: #64ffda;
}

.commentContainer {
    font-size: 1rem;
}

.inProgressText {
    font-size: 5rem;
    border-radius: 40px;
    background: #0b213f;
    box-shadow:  -10px 10px 20px #091528,
                    10px -10px 20px #102749;
}

.commentContainer {
    width: max-content;
    padding: 0.7rem;
    border-radius: 10px;
    background: #0b213f;
    box-shadow:  -5px 5px 10px #091528,
                5px -5px 10px #102749; 
}

.socialDiv {
    display: flex;
    gap: 30px;
    margin-top: 30px;
}

.socialIcon {
    width: 2rem;
    border-radius: 10px;
    background: #0b213f;
    padding: 0.2rem;
    box-shadow:  -5px 5px 10px #091528,
                5px -5px 10px #102749;
}

.socialIcon:hover {
    background: linear-gradient(225deg, #102749, #091528);
}


